import HTTP from "../http-common";

/**
 *      (Booking)
 *
 * @module getUserRequisites
 * @return {requisites} Promise
 */
export default (orderCode) =>
  HTTP.get("json/users/get_user_requisites", {
    params: { orderCode },
    headers: {
      // "api-version": 1.8 //   ,    
    },
    cache: false,
  });
