import HTTP from "../http-common";

/**
 *    
 *
 * @module getAvgReviewRatingForObject
 *
 * @param {number} objectId - ID 
 *
 * @return {object} Promise
 *
 */
export default (objectId) =>
  HTTP.get("json/messages/getAvgReviewRatingForObject", {
    params: { objectId },
    headers: {
      "api-version": 1.8, //   ,    
    },
    cache: false,
  });
