import HTTP from "../http-common";
import axios from "axios";

/**
 *         JSON,
 *   ./public/countries.json
 *
 * @module getLocations
 * @return {object} Promise
 */
export default (locationId, locationType) =>
  HTTP.get("/json/locations/getLocationByIdAndType", {
    params: {
      locationId,
      locationType
    },
    headers: {
      "api-version": 1.9, //   ,    
    },
    cache: false,
  });
