import HTTP from "../http-common";

/**
 *  
 *
 * @module auth
 * @param {object} payload   
 * @param {string} payload.name 
 * @param {string} payload.value  
 * @param {string} payload.password  
 * @param {boolean} payload.persistent [n=0] ?
 * @param {string} payload.status [n="auth-login"] ("auth-registration" - , "auth-login" -  )
 * @param {boolean} payload.forgot [n=0]  ?
 * @param {boolean} payload.terms [n=0]  ?
 * @return {object} Promise
 * @example
 *   auth({ value, password })
 */
export default (payload) => {
  let formData = new FormData();
  formData.append("name", payload.name || "");
  formData.append("value", payload.value);
  formData.append("password", payload.password || "");
  formData.append("persistent", payload.persistent || 0);
  formData.append("terms", payload.terms || 0);
  formData.append("forgot", payload.forgot || 0);
  formData.append("status", payload.status || "auth-login");
  formData.append("transport_notice", payload.transport_notice || "sms");
  // status: auth-registration
  return HTTP.post("json/users/auth", formData, {
    headers: {
      "api-version": String(null), //   ,    
    },
  });
};
