import HTTP from "../http-common";

/**
 *     
 *
 * @module getAvgReviewRatingForObject
 *
 * @param {number} objectId - ID 
 *
 * @return {object} Promise
 *
 */
export default (hotel_id) =>
  HTTP.get("json/messages/getHotelRating", {
    params: { hotel_id },
    headers: {
      "api-version": "1.10", //   ,    
    },
    cache: false,
  });
